import React, { Component } from "react";
import { Blob } from "react-blob";

import "./Nav.css";
import logo from "../Images/bhoanglogo.png";

const linkStyle = {
  fontWeight: "bold",
  color: "#d63230"
};

class Nav extends Component {
  constructor() {
    super();
    this.state = {
      navOpen: false
    };
  }

  toggleNav = () => {
    this.setState(prevState => {
      return { navOpen: !prevState.navOpen };
    });
  };

  changeRoute = route => {
    this.props.setRoute(route);
    this.toggleNav();
  };

  isNavOpen = () => {
    if (this.state.navOpen) {
      return { visibility: "visible", transform: "translateY(0)" };
    }
  };

  render() {
    return (
      <div className="navBarContainer">
        <div
          className="navOpen"
          onClick={this.toggleNav}
          style={{
            backgroundColor: !this.state.navOpen ? "#d63230" : "#39a9db"
          }}
        >
          {!this.state.navOpen ? "+" : "-"}
        </div>
        <div className="navBar" style={this.isNavOpen()}>
          <Blob
            size="80vh"
            style={{
              position: "absolute",
              top: "-10%",
              left: "-15%",
              zIndex: -1,
              backgroundColor: "white",
              color: "white",
              opacity: 0.1,
              fontSize: "10vh"
            }}
            src={logo}
          />

          <div className="navTextContainer">
            <div className="navText" onClick={() => this.changeRoute("home")}>
              Home
            </div>
            <div className="navText" onClick={() => this.changeRoute("skills")}>
              Skills
            </div>
            <div
              className="navText"
              onClick={() => this.changeRoute("projects")}
            >
              Portfolio
            </div>
            <div className="navText" onClick={() => this.changeRoute("about")}>
              About
            </div>
            <div
              className="navText"
              onClick={() => this.changeRoute("contact")}
            >
              Contact
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nav;



